@import 'utils/variables', 'utils/mixins';

.fileContainer {
  margin-top: 16px;
}

.actions {
  margin-top: 8px;

  display: flex;
  flex-direction: row;
  align-items: center;

  width: 100%;
  border: 1px dashed $base-dark-gray;
  padding: 24px;

  border-radius: 4px;

  &.active {
    background-color: #ebfff8;
    border-color: #00ed9c;
  }
}

.action {
  margin-right: 1px;
}

.action span {
  font-size: 16px;
  line-height: 24px;
}

.action button {
  height: 40px;
  width: auto;

  padding: 0 24px;

  white-space: nowrap;

  background: $base-gray;
  border-radius: 2px;
}

.fileList {
  display: flex;
  flex-flow: column;
}

.fileItem {
  margin-top: 4px;

  padding: 8px 16px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;

  border-radius: 4px;
  background: #efefef;

  box-sizing: border-box;

  cursor: pointer;

  @include darkTheme {
    color: $white;
    background: $black-blue;
  }
}

.fileItemRemove {
  margin-left: 16px;
  width: 12px;
  height: 12px;
  min-width: 12px;
  min-height: 12px;

  cursor: pointer;
}

.addFile {
  margin-top: 18px;
  width: 250px;
  height: 45px;

  @include respond-to('medium') {
    margin-top: 18px;
    width: 200px;
    height: 30px;
  }
}

.addFile span {
  font-size: 14px;
  line-height: 24px;

  @include respond-to('medium') {
    font-size: 12px;
    line-height: 14px;
  }
}

.title {
  font-size: 14px;
  color: $base-black;
  font-weight: 700;

  margin-bottom: 2px;
}

.comment {
  font-size: 14px;
  color: $base-middle-gray;
}

.filesTooltip {
  margin-left: 16px;
}

.fileIcon {
  width: 16px;
  height: 16px;
  min-width: 16px;
  min-height: 16px;

  margin-right: 16px;
}

.fileLabel {
  display: flex;
  align-items: center;
}
