@import 'utils/variables', 'utils/mixins';

.data_container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.data_input {
  display: flex;
  flex-direction: row;
  justify-content: space-between !important;
  align-items: flex-end;
}

.files {
  display: flex;
  flex-direction: row;
  justify-content: space-between !important;
  align-items: flex-end;

  padding-top: 30px;

  @include respond-to('medium') {
    padding-top: 40px;
  }
}

.red_star {
  color: #ff6161;
}

.data_table {
  width: 100%;
  font-size: 18px;
  padding-top: 10px;
  border-bottom: 1px solid #d2e8ff;

  @include respond-to('medium') {
    font-size: 12px;
  }
}

.data_table_header {
  text-align: left;
  font-weight: normal;
  display: contents;
  color: #064fa3;
  vertical-align: middle;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.data_table_header td {
  height: 60px;
  border-bottom: 1px solid #d2e8ff;
  font-weight: bold;

  @include respond-to('medium') {
    height: 45px;
  }
}

.data_table_tbody {
  color: #2a2f42;
  font-size: 16px;

  @include respond-to('medium') {
    font-size: 10px;
  }

  @include darkTheme {
    color: $white;
  }
}

.data_table_tbody td {
  height: 45px;

  @include respond-to('medium') {
    height: 40px;
  }
}

.data_big {
  width: 100%;
  margin-top: 16px;

  &.error input {
    border: 1px solid #ff6161;
  }
}

.data_label {
  font-size: 17px;
  line-height: 18px;
  color: #064fa3;

  @include respond-to('medium') {
    font-size: 11px;
    line-height: 13px;
  }
}

.file_list {
  margin-bottom: 14px;

  display: flex;
  flex-flow: row wrap;
}

.file_list_item {
  margin-top: 14px;
  margin-right: 11px;
  height: 45px;

  padding: 18px 20px;

  display: flex;
  flex-direction: row;
  align-items: center;

  font-size: 16px;
  line-height: 20px;

  border-radius: 8px;
  background: #ffffff;
  border: 1px solid #d2e8ff;
  box-sizing: border-box;
  box-shadow: 1px 1px 4px #a1c0e0;

  @include respond-to('medium') {
    margin-right: 10px;
    height: 30px;

    padding: 9px 10px;

    font-size: 10.5px;
    line-height: 12px;

    border: 0.5px solid #bcddff;
    box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.15);
  }
}

.file_list_item_remove {
  margin-left: 22px;
  width: 9px;
  height: 9px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 8px;
    height: 8px;
  }
}

.data_action_button {
  margin-top: 20px;
  margin-bottom: 10px;
  width: 200px;
  height: 35px;

  line-height: 24px;

  @include respond-to('medium') {
    width: 160px;
    height: 30px;
  }
}

.data_action_button span {
  font-size: 15px;

  @include respond-to('medium') {
    font-size: 10px;
  }
}

.labelBold {
  font-weight: 700;
  color: $base-black;
}

.labelThin {
  color: $base-middle-gray;
}

.dataHeader {
  color: $base-middle-gray;

  display: flex;
  justify-content: space-between;

  @include darkTheme {
    color: $grey;
  }
}

.data_small {
  width: 35%;
  margin-top: 16px;

  &.error input {
    border: 1px solid #ff6161;
  }
}
