@import 'utils/variables', 'utils/mixins';

.label {
  color: $base-black;

  @include darkTheme {
    color: $moderate-blue;
  }
}

.custom_input {
  margin-top: 4px;
  padding: 12px;
  height: 40px;
  width: 100%;

  font-size: 16px;

  outline: none;

  box-sizing: border-box;

  border: 1px solid $base-dark-gray;
  border-radius: 4px;

  &::selection {
    background-color: #d2e8ff;
  }

  @include darkTheme {
    background-color: $black-blue;
    color: $white;

    &:disabled {
      color: $grey;
      background-color: $black-grey-light;
    }
  }
}

.spanTooltip {
  font-size: 17px;

  @include respond-to('large') {
    font-size: 12px;
  }

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.required {
  color: $base-middle-gray;
}
