@import 'utils/variables', 'utils/mixins';

.container {
  padding: 16px 0 0;
}

input:disabled {
  background-color: #ffffff;
}

.header {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  padding: 30px 30px 0 0;

  @include respond-to('small') {
    padding: 20px 20px 0 0;
  }
}

.header_level2 {
  width: 100%;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  padding: 10px 30px 0 0;

  @include respond-to('small') {
    padding: 20px 20px 0 0;
  }
}

.header_text {
  margin-top: 0px;
  margin-left: 56px;

  font-size: $header;
  line-height: 40px;
  color: #0f3971;

  @include respond-to('large') {
    font-size: $headerLarge;
  }

  @include respond-to('medium') {
    margin-top: 0;
    margin-left: 30px;

    font-size: $headerMedium;
    line-height: 19px;
  }
}

.close_icon {
  margin-right: 30px;
  width: 24px;
  height: 24px;

  cursor: pointer;

  @include respond-to('medium') {
    width: 14px;
    height: 14px;
  }
}

.basic_information_container {
  padding-left: 56px;
  padding-right: 56px;

  @include respond-to('medium') {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.input_information_container {
  margin-bottom: 16px;
  padding: 0 24px;
}

.buttons {
  display: flex;
  justify-content: space-between;

  padding: 16px 24px;

  border-top: 1px solid $base-gray;
}

.data_action_button {
  margin-top: 50px;
  margin-bottom: 10px;
  margin-right: 15px;
  width: 300px;
  height: 45px;

  line-height: 24px;

  @include respond-to('medium') {
    margin-top: 30px;
    width: 200px;
    height: 30px;
  }
}

.data_action_button span {
  font-size: 16px;
  line-height: 14px;

  @include respond-to('medium') {
    font-size: 11px;
  }
}

.container_validation {
  font-weight: 300;
  color: #ff6161;
  font-size: 13px;
  line-height: 20px;
  padding: 8px 0;

  display: flex;
  align-items: flex-start;
}

.error_icon {
  margin-right: 10px;
  margin-top: 2px;

  min-width: 14px;
  min-height: 14px;
  max-height: 14px;
  max-width: 14px;
}

.button {
  width: 315px;
}
