@import '../../styles/utils/variables', '../../styles/utils/mixins';

.container {
  padding: 0 24px;
}

.header {
  color: #ff6161;
  font-size: 16px;
  font-weight: 600;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.item {
  font-weight: 300;
  color: #ff6161;
  font-size: 13px;
  line-height: 20px;
  padding: 8px 0;

  display: flex;
  align-items: flex-start;
}

.error_icon {
  margin-right: 10px;
  margin-top: 2px;
  width: 14px;
  height: 14px;
}
